export default Preloader;
function Preloader() {
        return (
            <div id='preloader' className="preloader">
                <img alt='preloader' src="https://i.pinimg.com/originals/b5/1b/02/b51b023b44d5e554b4eefb9c76b8a36c.gif"></img>
                <h1>Loading...</h1>
            </div>
        )
}

function preloaderOff() {
    setTimeout(() => {
      document.querySelector('.preloader').style.display = 'none';
    }, 3000);
  };
  
  preloaderOff();