import char from '../Img/Char.png';
import Diamond from '../Img/Diamond.png';
import React, { useRef, useEffect, ReactDOM } from 'react';
import axios from 'axios';


let clicks = 0;

function Main() {

    const myRef = useRef(null);

    useEffect(() => {
        myRef.current.addEventListener('click', handleClick);
    }, []);

    const handleClick = (e) => {

        let findCrit = document.querySelectorAll('.abg');
        findCrit.forEach(element => {
            element.remove();
        });

        if (criticalStrike() < 80) {
            clicks = clicks + 1;
        } else {
            let rand = 1 + Math.floor(Math.random() * (5 + 1 - 1));
            clicks = clicks + rand;
            let crimg = document.createElement('img');
            crimg.src = 'https://cdn-icons-png.flaticon.com/512/14697/14697202.png';
            crimg.style.position = 'absolute';
            crimg.className = 'abg'
            crimg.width = '70';
            crimg.height = '70';
            crimg.style.left = e.clientX + 'px';
            crimg.style.top = e.clientY + 'px';
            document.querySelector('.main').appendChild(crimg);
        }
        axios.post(`https://fsdfsdfsf.s-host.net/aff.php`, { clicks })
            .then(res => {
                console.log(res);
                console.log(res.data);
            })
            .catch(err => {
                console.error(err);
            });
        document.querySelector('.clickss').innerHTML = clicks;

    };

    function criticalStrike() {
        let randCrit = 1 + Math.random() * (100 + 1 - 1);
        return randCrit;
    }


    return (
        <div className="main">
            <div className="char_div">
                <img ref={myRef} className='char' src={char}></img>
            </div>
            <div className="values-money">
                <p><img className='diamond' src={Diamond}></img>Gems:<span className='clickss'>{clicks}</span></p>
            </div>
            <div className="upgrade-clicks">
                <button ><a>Upgrade click</a></button>
            </div>
        </div>
    )
}




export default Main;